@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&family=Montserrat:wght@500&display=swap');

html {
    font-size: 62.5%;
    height: 100%;
    background: #F1EAD2;
    padding: 7%;
    min-height: 100vh;
}

body {
    font-family: Josefin Sans;
    font-weight: 600;
    font-size: 1.8rem;
    line-height: 1.5;
    margin: 0;
    color: rgb(0, 0, 0);
    height: 100%;
    border-radius: 10%;

    #root {
        height: 100%;

        .container{
            height: 100%;
            width: 100%;
            margin: 0;
        }
    }
}

img{
    max-width: 100%;
}
